import Axios, { AxiosInstance } from 'axios';
import * as React from 'react';
import { Alert, Button, FormGroup, Input, Label } from 'reactstrap';

import * as constants from '../../constants';
import './settings.css';

interface SettingsPageState {
  load: boolean,
  compare: string,
  target: string
}

export class SettingsPage extends React.Component<any, SettingsPageState> {
  public state: SettingsPageState = {
    load: false,
    compare: "",
    target: ""
  }

  private axios: AxiosInstance = Axios.create({
    baseURL: constants.API_SERVER_URL
  });

  public componentDidMount = () => {
    this.axios.get("/api/list")
      .then((resp) => {
        const data: any = resp.data;
        if (data.result === 1) {
          this.setState({
            load: true,
            compare: "",
            target: ""
          });
        }
        else {
          this.setState({
            load: true,
            compare: data.compare.join("\n"),
            target: data.target.join("\n")
          });
        }
      })
      .catch((err) => {
        alert("    .   .")
      });
  }

  public renderLoad = () => (
    <div>
       ...
    </div>
  );

  public onCompareChange = (event: any) => {
    this.setState({
      compare: event.currentTarget.value
    });
  }

  public onTargetChange = (event: any) => {
    this.setState({
      target: event.currentTarget.value
    });
  }

  public save = () => {
    const {compare, target} = this.state
    this.axios.post("/api/list", {
      compare: compare.split("\n"), 
      target: target.split("\n")}
    )
      .then((resp) => {
        const data: any = resp.data
        if (data.result === 1) {
          alert("  Crawling  .\n       .");
        } else {
          alert(".\nCrawling      .")
        }
        location.reload();
      })
      .catch((err) => {
        alert("    .   .")
      });
  }

  public render() {
    if (!this.state.load) {
      return this.renderLoad();
    }
    return (
      <div className="setting__wrapper">
        <h3>Repository  </h3>
        <p>
          Repository  .      Repository . <br />
           Repository  , <code>owner/project_name</code>  .<br />
               Repository ,        .
        </p>
        <Alert color="danger">
          <b>!</b>        .
        </Alert>
        <hr />
        <FormGroup>
          <Label for="compare"> Repository</Label>
          <Input type="textarea" onChange={this.onCompareChange} value={this.state.compare} name="text" id="compare" rows={5}/>
        </FormGroup>
        <FormGroup>
          <Label for="target">  Repository</Label>
          <Input type="textarea" onChange={this.onTargetChange} value={this.state.target} name="text" id="target" rows={5}/>
        </FormGroup>
        <Button color="primary" onClick={this.save}></Button>
      </div>
    );
  }
}



