import Axios, { AxiosInstance } from 'axios';
import * as React from 'react';
import { Badge, Button } from 'reactstrap';
import { ApiCrawlInfoResponse } from 'src/types';

import * as constants from '../../constants';
import './update.css';

interface UpdatePageState {
  timestamp: number,
  is_ongoing: boolean
}

export class UpdatePage extends React.Component<any, UpdatePageState> {
  public state: UpdatePageState = {
    timestamp: -1,
    is_ongoing: false
  }

  private axios: AxiosInstance = Axios.create({
    baseURL: constants.API_SERVER_URL
  });

  public componentDidMount = () => {
    this.axios.get("/api/crawl")
      .then((resp) => {
        const data: ApiCrawlInfoResponse = resp.data;
        this.setState({
          timestamp: data.timestamp,
          is_ongoing: data.is_ongoing
        });
      })
      .catch((err) => {
        alert("    .   .")
      });
  }

  public startCrawling = () => {
    this.axios.post("/api/crawl")
      .then((resp) => {
        const data: any = resp.data;
        if (data.status === 0) {
          alert("Crawling Job .\n     .");
          location.reload();
        } else {
          alert("   Crawling Job  .\n !");
        }
      })
  }

  public renderLoad = () => (
    <p> ...</p>
  )

  public renderContent = () => (
    <div>
      <span>   : {new Date(this.state.timestamp * 1000).toString()}</span>
      <br />
      <span>   : </span> 
      {
        (this.state.is_ongoing) ? <Badge color="danger">Crawling  </Badge>
        : <Badge color="success"></Badge>
      }
      <br /><br />
      <Button color="primary" disabled={this.state.is_ongoing} onClick={this.startCrawling}>Crawling </Button>
    </div>
  )

  public render() {
    if (this.state.timestamp === -1) {
      return this.renderLoad();
    }
    return (
      <div className="update__wrapper">
        <h3>  </h3>
        <p>       DB .</p>
        <hr />
        {this.renderContent()}
      </div>
    );
  }
}



