import Axios, { AxiosInstance } from 'axios';
import * as React from 'react';
import { Bar, Doughnut } from 'react-chartjs-2';
import { Report } from 'src/types';

import * as constants from '../../constants';
import './report.css';

interface ReportPageState {
  load: boolean,
  report?: Report,
  timestamp?: number,
  is_ongoing?: boolean
}

export class ReportPage extends React.Component<any, ReportPageState> {
  public state: ReportPageState = {
    load: false,
    report: undefined
  };

  private axios: AxiosInstance = Axios.create({
    baseURL: constants.API_SERVER_URL
  });
  
  public componentDidMount = () => {
    const {owner, project} = this.props.match.params;
    this.axios.post("/api/detail", {repo: owner + "/" + project})
      .then((resp) => {
        const data: any = resp.data;
        if (data.result === 1) {
          this.setState({
            load: true,
            report: undefined,
            timestamp: undefined,
            is_ongoing: undefined
          });
        } else {
          this.setState({
            load: true,
            report: data.detail,
            timestamp: data.timestamp,
            is_ongoing: data.is_ongoing
          });
        }
      })
      .catch((err) => {
        alert("    .   .")
      });
  }

  public renderLoad = () => (
    <div>
       ...
    </div>
  );
  
  public decideColor = (data: number[]) => {
    const sum = data.reduce((p, c) => p + c, 0);
    const result: string[] = [];
    data.forEach(v => {
      const percent = v / sum;
      result.push(`rgba(20, 29, 37, ${percent + 0.1}`);
    });
    return result;
  }
  
  public render() {
    if (!this.state.load) {
      return this.renderLoad();
    }
    if (this.state.report === undefined) {
      alert("   .\nCrawling .");
      location.href = "/";
      return;
    }

    // Fundamentals
    const report: Report = this.state.report;
    const timestamp: Date = new Date(this.state.timestamp! * 1000);

    // Date string by locale
    const reportDate: string = (this.state.is_ongoing!) ? " Crawling .   ." : timestamp.toLocaleDateString() + " " + timestamp.toLocaleTimeString();

    // Community profiles
    const totalCommunityProfiles = Object.keys(report.community_profiles).length;
    const checkedCommunityProfiles = Object.keys(report.community_profiles).filter(
      profileName => report.community_profiles[profileName] === true
    ).length;

    // Language string
    const languages = report.languages.map(v => {
      const {name, percent} = v;
      return  name + " (" + percent + "%)";
    }).join(", ");

    // Contributor metrics
    const contributorLabel = Object.keys(report.contributors).sort(
      (keyA: string, keyB: string) => report.contributors[keyB] - report.contributors[keyA]
    ).slice(0, 30);
    const contributorValue: number[] = contributorLabel.map(v => {
      return report.contributors[v];
    });

    // Commit metrics
    const commitTotal: number[] = report.commit_graph.total;
    const commitLabel: string[] = report.commit_graph.week.map(v => {
      const realDate = new Date(v * 1000);
      return (realDate.getMonth() + 1).toString() + "/" + (realDate.getDate().toString());
    });

    // Find index for cutting zero data
    let cuttingIndex = 0;
    for (let i = 0; i < commitTotal.length; i++) {
      if (commitTotal[i] > 0) {
        cuttingIndex = i;
        break
      }
    }
    commitTotal.splice(0, cuttingIndex);
    commitLabel.splice(0, cuttingIndex);

    const commitStartDateText = commitLabel[0];
    const commitEndDateText = commitLabel[commitLabel.length - 1];


    return (
      <div className="container report__wrapper">
        <h1> : <span className="text-gray">{report.name}</span></h1>
        <hr/>
        <div className="block-container">
          <h3 className="block-title"> </h3>
          <table className="table table-bordered table-striped">
            <tbody>
              <tr>
                <th scope="row"></th>
                <td>{report.name}</td>
              </tr>
              <tr>
                <th scope="row">Repository </th>
                <td><a href={"https://github.com/" + report.name}>https://github.com/{report.name}</a></td>
              </tr>
              <tr>
                <th scope="row"> Profile </th>
                <td><a href={"https://github.com/" + report.name.split("/")[0]}>https://github.com/{report.name.split("/")[0]}</a></td>
              </tr>
              <tr>
                <th scope="row">  </th>
                <td>{reportDate}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <div className="block-container">
          <h3 className="block-title">Repository </h3>
          <table className="table table-bordered table-striped">
            <tbody>
              <tr>
                <th scope="row">License</th>
                <td>{report.license}</td>
              </tr>
              <tr>
                <th scope="row">Languages</th>
                <td>{languages}</td>
              </tr>
              <tr>
                <th scope="row">Commit <span className="badge badge-primary"> </span></th>
                <td> <strong>{report.commits.toLocaleString()}</strong></td>
              </tr>
              <tr>
                <th scope="row">Vaild Commit <span className="badge badge-primary"> </span></th>
                <td><strong>{report.per_valid_commit}%</strong></td>
              </tr>
              <tr>
                <th scope="row">Contributor <span className="badge badge-primary"> </span></th>
                <td> <strong>{report.contributors_count.toLocaleString()}</strong> Official Contributor</td>
              </tr>
              <tr>
                <th scope="row">Issue <span className="badge badge-primary"> </span></th>
                <td> <strong>{(parseInt(report.issue_open, 10) + parseInt(report.issue_closed, 10)).toLocaleString()}</strong>: {parseInt(report.issue_open, 10).toLocaleString()} , {parseInt(report.issue_closed, 10).toLocaleString()} </td>
              </tr>
              <tr>
                <th scope="row">Pull Request <span className="badge badge-primary"> </span></th>
                <td> <strong>{(parseInt(report.pr_open, 10) + parseInt(report.pr_closed, 10)).toLocaleString()}</strong>: {parseInt(report.pr_open, 10).toLocaleString()} , {parseInt(report.pr_closed, 10).toLocaleString()} </td>
              </tr>
              <tr>
                <th scope="row">Pull Request (Approved) <span className="badge badge-primary"> </span></th>
                <td> <strong>{report.pr_approved}</strong></td>
              </tr>
              <tr>
                <th scope="row">Community Profile <span className="badge badge-primary"> </span></th>
                <td> {totalCommunityProfiles}  <strong>{checkedCommunityProfiles}</strong> </td>
              </tr>
              <tr>
                <th scope="row">OPEG </th>
                <td><strong>{report.opeg.toLocaleString()}</strong></td>
              </tr>
            </tbody>
          </table>
        </div>
        <div className="block-container">
          <h3 className="block-title">Community Profile </h3>
          <table className="table table-bordered table-striped">
            <tbody>
              <tr>
                <th scope="row" style={{ width: '30%' }}>README</th>
                <td className="col-9">{report.community_profiles.README ? '': ''}</td>
              </tr>
              <tr>
                <th scope="row">CODE_OF_CONDUCT</th>
                <td>{report.community_profiles.CODE_OF_CONDUCT ? '': ''}</td>
              </tr>
              <tr>
                <th scope="row">LICENSE</th>
                <td>{report.community_profiles.LICENSE ? '': ''}</td>
              </tr>
              <tr>
                <th scope="row">CONTRIBUTING</th>
                <td>{report.community_profiles.CONTRIBUTING ? '': ''}</td>
              </tr>
              <tr>
                <th scope="row">ISSUE_TEMPLATE</th>
                <td>{report.community_profiles.ISSUE_TEMPLATE ? '': ''}</td>
              </tr>
              <tr>
                <th scope="row">PULL_REQUEST_TEMPLATE</th>
                <td>{report.community_profiles.PULL_REQUEST_TEMPLATE ? '': ''}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <div className="block-container">
          <h3 className="block-title">   (Top 50)</h3>
          <Doughnut
            data={{
              labels: contributorLabel,
              datasets: [{
                label: "Contribution (Commits)",
                data: contributorValue,
                backgroundColor: this.decideColor(contributorValue)
              }]
            }}/>
        </div>
        <div className="block-container">
          <h3 className="block-title">  Commit  ({`${commitStartDateText}~${commitEndDateText}`})</h3>
          <Bar
            data={{
              labels: commitLabel,
              datasets: [{
                label: "Commits of the week",
                data: commitTotal,
                backgroundColor: '#34495e',
                borderColor: '#2c3e50',
                borderWidth: 1
              }]
            }}/>
        </div>
      </div>
    )
  }
}



